<template lang="html">
    <div id="map"></div>
</template>

<script>
import L from 'leaflet'
export default {
  name: 'MyMap',
  mounted() {
    this.initMap();
  },
  computed:{
    dataset(){
      return this.$store.state.dataset
    },
    iconSize(){
      return this.$store.state.iconSize
    }
  },
  watch:{
    dataset(){
      this.refreshMap()
    },
    iconSize(){
      this.refreshMap()
    }
  },
  methods:{
    initMap(){
      this.map = L.map('map').setView([0, 0], 3)
      this.tileLayer = L.tileLayer(
        'https://server.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer/tile/{z}/{y}/{x}',
        {
          maxZoom: 18,
          attribution: 'ESRI'
        }
      )
      this.updateBounds()
      this.tileLayer.addTo(this.map)
      this.map.on("moveend", () => {
        this.updateBounds()
      });
    },
    refreshMap(){
      if (this.map.hasLayer(this.iconLayer)) {
        this.map.removeLayer(this.iconLayer)
      }
      this.iconLayer = L.layerGroup()
      for (let d of this.dataset) {
        const icon = L.icon({
            iconUrl: d.path,
            iconSize: [this.iconSize, this.iconSize*1.5]
        });
        L.marker([d.y, d.x], {icon: icon}).addTo(this.iconLayer).bindPopup(d.name);
      }
      this.iconLayer.addTo(this.map)
    },
    updateBounds(){
      const bounds = this.map.getBounds()
      this.$store.commit('updateBounds', bounds)
    }
  }
}
</script>

<style lang="css">
#map{
  height: 100%;
  width:100%;
}
</style>
