<template>
  <div id="app">
    <b-container fluid id="main-container">
      <b-row id="first-row" class="text-center">
        <b-col class="collapse-container">
          <img alt="MSF logo" :src="`${this.imgURL}logo-msf.svg`">
          <b-button v-b-toggle.collapse-1 variant="dark">Menu</b-button>
            <b-collapse id="collapse-1" class="mt-2">
              <b-card>
                <b-row class="text-center">
                  <b-col>
                    <b-button variant="info" @click="up">Augmenter</b-button>
                    <b-button variant="dark" @click="down">Diminuer</b-button>
                  </b-col>
                </b-row>
                <b-row class="text-center">
                  <b-col>
                    <span>Taille: {{ size }}</span>
                  </b-col>
                </b-row>
                <b-row align-h="center">
                  <b-col cols="4">
                    <b-button variant="success" @click="generate">Générer des icones</b-button>
                    <b-button size="sm" @click="selectAllRows">Select all</b-button>
                    <b-button size="sm" @click="clearSelected">Clear selected</b-button>
                    <b-table
                      ref="selectableTable"
                      selectable
                      select-mode="multi"
                      :items="items"
                      :fields="fields"
                      @row-selected="onRowSelected"
                      responsive="sm"
                      sticky-header
                      head-variant="light"
                    >
                      <template v-slot:cell(selected)="{ rowSelected }">
                        <template v-if="rowSelected">
                          <span aria-hidden="true">&check;</span>
                          <span class="sr-only">Selected</span>
                        </template>
                        <template v-else>
                          <span aria-hidden="true">&nbsp;</span>
                          <span class="sr-only">Not selected</span>
                        </template>
                      </template>
                      <template slot="icon" slot-scope="{ item: { icon }}">
                        <img :src="icon">
                    </template>
                    </b-table>
                  </b-col>
                </b-row>
              </b-card>
            </b-collapse>
        </b-col>
      </b-row>
      <b-row id="second-row">
        <Mymap />
      </b-row>
    </b-container>
  </div>
</template>

<script>
import Mymap from './components/map.vue'
const devEnv = process.env.NODE_ENV === 'development'

export default {
  name: 'app',
  components: {
    Mymap
  },
  data(){
    return {
      fields: ['selected','name'],
      items(){
        const iconsList = this.$store.state.icons
        const array = []
        for (let i of iconsList) {
          const temp = {name: i.path}
          array.push(temp)
        }
        return array
      },
    }
  },
  computed:{
    size(){
      return this.$store.state.iconSize
    },
    selected:{
      get: function() {return this.$store.state.selected},
      set: function(value) {this.$store.commit('updateSelection', value)}
    },
    imgURL(){
      const imgURL = devEnv ? '/images/' : `./images/`
      return imgURL
    }
  },
  methods:{
    up(){
      this.$store.commit('upIconSize')
    },
    down(){
      this.$store.commit('downIconSize')
    },
    onRowSelected(items) {
      this.selected = items
    },
    selectAllRows() {
      this.$refs.selectableTable.selectAllRows()
    },
    clearSelected() {
      this.$refs.selectableTable.clearSelected()
    },
    generate(){
      this.$store.commit('generateDataset')
    }
  }
}
</script>

<style>
#app {
  height: 100%;
  width: 100%;
}
#main-container{
  height: 100%;
}
#first-row{
  height:5%;
}
#second-row{
  height:95%;
  z-index: 500;
}
#collapse-1{
  z-index: 1001;
  position: relative;
  height: 80%;
  width: 100%;
}
.collapse-container{
  padding-left: 0;
  padding-right: 0;
}
</style>
